.sidemenu {
  width: 20%;
  height: fit-content;
  position: absolute;
  top: 12%;
  margin: 10px;
}
.filter {
  height: fit-content;

  border: 0.5px solid rgb(133, 129, 129, 0.6);
  font-weight: bold;
  font-size: large;
  text-transform: uppercase;
  font-size: 20px;

  padding: 10px;
  padding-right: 15px;
}
.appliedFiltes {
  border: 0.5px solid rgb(133, 129, 129, 0.6);
  height: 300px;
  overflow: scroll;
  text-align: center;
}
.element-container {
  border-radius: 10px 10px;
  height: 25px;
  width: fit-content;
}
element {
  margin-left: 2px;
  padding: 2px;
}
.cancel {
  position: absolute;
  right: 23px;
  cursor: pointer;
}
.clearall {
  padding: 5px;
  border-radius: 1px 1px;
  background-color: white;
  cursor: pointer;
}
.clearall:hover {
  background-color: rgb(223, 14, 14);
  border: none;
}
